
html, body {
	min-height: 100%;
	height: 100%;
	width: 100vw;
	max-width: 100vw;
    margin: 0;
    padding: 0;
	box-sizing: border-box;
    overflow-x: hidden;
	color: $fontColor;
    font-family: $font;
    background: $colorBackground;
    font-size: $fontBaseSize;
}

* {
	box-sizing: border-box;
}

a {
	color: $colorLink;
	transition: color .2s ease;

	&:hover {
		color: lighten($colorLink, 15%);
	}
}
